import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta:{title:"首页"}
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
    meta:{title:"关于"}
  },
  {
    path: '/uniondrughealthy/addjs',
    name: 'UniondrugAddJS',
    component: () => import('../views/uniondrug/UniondrugAddJS.vue'),
    meta:{title:"药联健康JS"}
  },
  {
    path: '/uniondrughealthy/pagejump',
    name: 'UniondrugPageJump',
    component: () => import('../views/uniondrug/UniondrugPageJump.vue'),
    meta:{title:"药联健康页面"}
  }
]



const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
